import React, {Component} from 'react'
import {Link, Route, Redirect} from 'react-router-dom'
import {connect} from 'react-redux'
import classnames from 'classnames'

import './login.scss'
import Account from './subpage/Account'
import PhoneLogin from './subpage/PhoneLogin'

class Login extends Component {
  componentWillMount() {
    // 已登录，不能访问登录页面
    if (this.props.auth.isLogin) {
      this.props.history.goBack()
    }
  }
	render() {
		/**
		 * v4 包含路由 和 精准路由
		 */
    const { from } = this.props.location.state || { from: { pathname: '/' } }
    if (this.props.auth.isLogin === true) {  // 已登录带回原始路由
      return <Redirect to={from} />
    }
    var path = this.props.location.pathname
		return (
			<div>
				<div className="login-wrap">
					<div className="login-title"></div>
					<div className="login-form">
						<div className="form">
							<div className="form-t">
								<Link to="/login">
									<div className={path === '/login' ? 't-l f1' : 't-l f2'}>手机登录</div>
								</Link>
								<Link to="/login/account">
									<div className={path === '/login/account' ? 't-l f1' : 't-l f2'}>账户登录</div>
								</Link>
							</div>
							<main>
								<Route exact path="/login/account" component={Account} />
								<Route exact path="/login" component={PhoneLogin} />
							</main>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth
	}
}

export default connect(mapStateToProps, null)(Login);
